import React, { useState, useEffect, useRef } from "react";
import Scene from "./Scene";
import data from "../data/copy.json";
import HeadContainer from "./headContainer.js";
import SoundControl from "./SoundControl";
import Step from "./Step.js";
import { animationTime, audioData, allDeathToll } from "./data.js";
import Audio from './Audio';
import DeathTollDiv from './DeathTollDiv';
import Tooltip from './Tooltip';
import { victimData, stepTooltipArr, tooltipPos } from "./data";
import randomNumArr from "../data/randomArr.json";
import njCovidData from "../data/northjersey_covid_data.json";
import SearchBar from './SearchBar';
import njDeathData from '../data/northjersey_covid_data.json';
import CreditCard from './CreditCard';




function MainCtn(props) {
    const njCovidArr = njCovidData["names"];
    const {hed, dek} = data;

    // console.log("njCovidArr", njCovidArr);
    const [currentStepIndex, setCurrentStepIndex] = useState(null);
    const [startTime, setStartTime] = useState(performance.now())
    let [clickedIdx, setClickedIdx] = useState();
    // This callback fires when a Step hits the offset threshold. It receives the
    // data prop of the step, which in this demo stores the index of the step.
    let stepData = data.content.filter(t => t.type === "step");
    let step = stepData[0].value;
    const stepCtnRef = useRef(null);

    const creditRef = useRef();
    const COUNT = allDeathToll;
    console.log('allDeathToll', allDeathToll)
    const [audioEnabled, setAudioEnabled] = useState(false);

    useEffect(() => {
        showStep();
        setStartTime(performance.now());
    }, [currentStepIndex]);



    let i = 1;
    let timer;

    function showStep() {
        let stepCtn = stepCtnRef.current;
        let { children } = stepCtn;
        let ctnArr = [...children];

        ctnArr.forEach((child, i) => {
            if (i == currentStepIndex) {
                child.classList.add("active");
            } else if (child.classList.contains("active")) {
                child.classList.remove("active");
            }
        });
    }

    function onClickFn(prop) { // run the animation
        document.querySelector(".head_ctn").classList.remove("active");
        setCurrentStepIndex(0);
        
        setAudioEnabled(true);
        // run the animation
        let idx = 0;
        let timer;
        loop();
        function loop(){
            if(idx==0){
                document.querySelector('#introAudio').play();
            } else if(idx==1){
                document.querySelector("#mainAudio").play();
            } else if(idx==11){
                const endAudio = document.querySelector("#endingDrone");
                endAudio.loop = true;
                endAudio.play();
            }
            setCurrentStepIndex(idx);
            timer = setTimeout(loop, animationTime[idx] * 1 * 1000);
            idx++;
            if (idx >= animationTime.length) {
                clearTimeout(timer);
                setTimeout(t => {
                    document.querySelector(".step__outer__11").classList.remove('active');
                    return
                }, 2 * 1000);
                return
            }
        }

    }

    function onSouncControlClick(){
        setAudioEnabled(!audioEnabled);
    }

    return (
        <div className="main__ctn">
            <HeadContainer
                onClickFn={onClickFn}
                currentStepIndex={currentStepIndex}
                hed={hed}
                dek={dek}
            />
            { audioData.map(obj=>{
                let {key, src} = obj;
                return (
                    <Audio
                        id={key}
                        src={src}
                        audioEnabled={audioEnabled}
                    />
                );
            })}
            

            <CreditCard data={data.credits} ref={creditRef} />
            {
                <div
                    className={`tooltip_ctn tooltip_ctn__100`}
                    data-step={100}
                    key={`tooltip_ctn__${100}`}
                    data-posx={-250.5}
                    data-posy={200}
                    style={{
                        position: "absolute",
                        top: "-50%",
                        left: "-50%",
                        color: "#ababab"
                    }}
                >
                    <p className="tooltip tooltipHeader"></p>
                    <p
                        className="tooltip tooltipLoc"
                        id={`tooltip__loc__${100}`}
                    ></p>
                </div>
            }

            {stepTooltipArr[currentStepIndex] &&
                stepTooltipArr[currentStepIndex].length &&
                stepTooltipArr[currentStepIndex].map((t, i) => {
                    return (
                        <Tooltip
                            key={`tooltip_step_${currentStepIndex}_${t}`}
                            count={i}
                            // display one by one
                            data={victimData[t - 1]}
                            stepIndex={2}
                            pos={{
                                x: tooltipPos[t - 1].x,
                                y: tooltipPos[t - 1].y,
                                z: 0
                            }}
                            currentStepIndex={currentStepIndex}
                        />
                    );
                })}

            {/* {

            } */}

            {/* graphic ctn */}
            <div
                id="graphic_ctn_outer"
                style={{
                    position: "sticky",
                    top: 0,
                    height: "100vh"
                }}
            >
                <SoundControl
                    step={currentStepIndex}
                    onSouncControlClick={onSouncControlClick}
                    audioEnabled={audioEnabled}
                />
                <DeathTollDiv currentStepIndex={currentStepIndex} />
                {/* <Timeline step={currentStepIndex} /> */}

                <section id="graphic_ctn">
                    {currentStepIndex >= 11 && (
                        <SearchBar
                            data={njDeathData}
                            setClickedIdx={setClickedIdx}
                        />
                    )}
                    <Scene
                        step={currentStepIndex}
                        randomNumArr={randomNumArr}
                        COUNT={COUNT}
                        startTime={startTime}
                        njCovidArr={njCovidArr}
                        clickedIdx={clickedIdx}
                    />
                </section>
                <div className="step__ctn" ref={stepCtnRef}>
                    {step.map((data, stepIndex) => (
                        <Step
                            key={`step__${stepIndex}`}
                            data={data}
                            stepIndex={stepIndex}
                            currentStepIndex={currentStepIndex}
                        />
                    ))}
                </div>
            </div>
        </div>
    );
}

export default MainCtn;
